.special {
  background: rgba(255, 100, 155, 0.2); }

.historyIndex {
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.5);
  color: rgba(255, 255, 255, 0.7);
  padding: 5px;
  width: 1.5rem;
  margin: 0.25rem; }

.icon-container {
  width: auto;
  height: auto;
  padding: 0.25rem 1rem;
  margin: 0 0.5rem;
  background: #225599aa;
  transition: all 0.25s;
  display: flex;
  align-items: center;
  border-radius: 5px; }
  .icon-container:hover {
    background: #22c5ffaa;
    color: #225599; }

#saveHistory {
  background-image: url("../../assets/diskette.png");
  background-size: 75%;
  background-repeat: no-repeat;
  background-position: center;
  width: 3rem;
  height: 3rem; }

.flex-row {
  display: flex;
  align-items: center;
  justify-content: center; }

.hovered {
  width: 3rem;
  height: 3rem;
  position: relative;
  display: flex; }

.addFavorite {
  background: url("../../assets/star.png");
  background-size: 75%;
  background-repeat: no-repeat;
  background-position: center;
  width: 3rem;
  height: 3rem;
  position: absolute;
  right: 0;
  display: flex;
  z-index: 99;
  transition: 0.25s all; }
  .addFavorite:hover {
    background: url("../../assets/bookmarks.png");
    background-size: 75%;
    background-repeat: no-repeat;
    background-position: center;
    width: 3rem;
    height: 3rem;
    position: absolute;
    right: 0;
    transition: 0.25s all; }
</style>

