      <div class="history-listing">
        {#each favorites as dir, i}
          <div class="historyIndex">{i}</div>
          <div
            class="dir i {navHistoryLocation === i ? 'special' : 'none'}"
            on:click={e => navigate(e, dir, 'historyItem')}>
